"use client";

import { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { FaShoppingCart } from "react-icons/fa";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import NavItem from "./ChildComponent/NavItem";
import MobileMenu from "./ChildComponent/MobileMenu";

const Navbar = () => {
  const pathname = usePathname();
  const isHomePage = pathname === "/" || pathname === "/home";

  const [openDropdown, setOpenDropdown] = useState(null);
  const [showBg, setShowBg] = useState(!isHomePage);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const timeoutRef = useRef();

  const handleMouseEnter = (name) => {
    clearTimeout(timeoutRef.current);
    setOpenDropdown(name);
  };

  const handleMouseLeave = () => {
    timeoutRef.current = setTimeout(() => setOpenDropdown(null), 100);
  };

  useEffect(() => {
    if (!isHomePage) return; // only apply scroll behavior in homepage

    const hero = document.querySelector(".hero-section");
    const handleScroll = () => {
      if (!hero) return;
      const heroBottom = hero.getBoundingClientRect().bottom;
      setShowBg(heroBottom <= 80);
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, [isHomePage]);

  return (
    <nav
      className={`fixed top-0 left-0 w-full z-50 transition-all duration-300 border-b ${
        showBg ? "bg-white text-black shadow-md" : "bg-transparent text-white"
      }`}
    >
      <div
        className="max-w-7xl mx-auto px-2 py-4 flex items-center justify-between"
        style={{ height: "80px" }}
      >
        {/* Logo */}
        <Link href="/">
          <div className="flex items-center space-x-2 cursor-pointer">
            <Image src="/img/logo5.png" alt="Logo" width={120} height={120} />
          </div>
        </Link>

        {/* Nav Items - Desktop */}
        <ul className="hidden md:flex space-x-10 h-20 items-center">
          {NavItem.map((item) => (
            <li
              key={item.name}
              className="relative"
              onMouseEnter={() => handleMouseEnter(item.name)}
              onMouseLeave={handleMouseLeave}
            >
              <Link
                href={item.href || "#"}
                className="cursor-pointer hover:text-blue-400 select-none"
              >
                {item.name} {item.dropdown && "▾"}
              </Link>

              {/* Optional spacer to prevent flicker */}
              <div className="absolute top-full left-0 w-full h-5"></div>

              <AnimatePresence>
                {openDropdown === item.name && item.dropdown && (
                  <motion.ul
                    initial={{ maxHeight: 0 }}
                    animate={{
                      maxHeight: 600, // dropdown এর সর্বোচ্চ উচ্চতা (প্রয়োজনে adjust করো)
                      transition: { duration: 0.9, ease: "easeOut" },
                    }}
                    exit={{
                      maxHeight: 0,
                      transition: { duration: 0.03, ease: "easeIn" },
                    }}
                    className={`mt-7 ${
                      item.dropdown.length > 6
                        ? "fixed left-0 right-0 p-6 grid grid-cols-4 gap-x-6 gap-y-3 bg-white shadow-xl"
                        : "absolute left-0 top-full mt-0 flex flex-col w-auto bg-white shadow-xl"
                    }`}
                    // position-related styles আগের মতোই থাকবে
                    style={{
                      overflowY: "auto",
                      zIndex: 9999,
                      maxWidth:
                        item.dropdown.length > 6 ? "100vw" : "max-content",
                      minWidth: item.dropdown.length > 6 ? "100%" : "auto",
                      overflow: "hidden",
                    }}
                  >
                    {item.dropdown.map((subItem, index) => (
                      <li key={subItem.name + index}>
                        <Link
                          href={subItem.href || "#"}
                          className="relative block px-4 hover:bg-gray-100 text-black hover:text-blue-400 py-2 whitespace-nowrap bg-white text-base font-medium duration-300"
                          onClick={(e) => {
                            if (!subItem.href) e.preventDefault();
                          }}
                        >
                          {subItem.name}
                        </Link>
                      </li>
                    ))}
                  </motion.ul>
                )}
              </AnimatePresence>
            </li>
          ))}
        </ul>

        {/* Buy Now Button */}
        <button
          className={`hover:bg-blue-400 border px-2 py-2 hidden md:flex items-center gap-2 ${
            showBg
              ? "bg-white text-black hover:text-white"
              : "bg-transparent text-white"
          }`}
        >
          <FaShoppingCart className="text-lg" />
          Buy Now
        </button>

        {/* Hamburger for Mobile */}
        <div
          className="md:hidden text-xl cursor-pointer select-none"
          onClick={() => setIsMobileMenuOpen(true)}
        >
          ☰
        </div>
      </div>

      {/* Mobile Slide Menu */}
      <MobileMenu
        isOpen={isMobileMenuOpen}
        onClose={() => setIsMobileMenuOpen(false)}
        navItems={NavItem}
      />
    </nav>
  );
};

export default Navbar;
